$.components.register("dataTable", {
    defaults: {
        responsive: true,
        language: {
            "sProcessing": "Procesando...",
            "sLengthMenu": "Mostrar _MENU_ registros",
            "sZeroRecords": "No se encontraron resultados",
            "sEmptyTable": "Ningn dato disponible en esta tabla",
            "sLoadingRecords": "Cargando...",
            "sInfo": "Mostrando registros del _START_ al _END_ de un total de _TOTAL_ registros",
            "sInfoEmpty": "Mostrando registros del 0 al 0 de un total de 0 registros",
            "sInfoFiltered": "(filtrado de un total de _MAX_ registros)",
            "sInfoPostFix": "",
            "sSearch": "Buscar:",
            "sUrl": "",
            "sInfoThousands": ",",
            "sSearchPlaceholder": "Buscar..",
            "lengthMenu": "_MENU_",
            "search": "_INPUT_",
            "oPaginate": {
                "sFirst": "Primero",
                "sLast": "ltimo",
                "sNext": '<i class="icon md-chevron-right"></i>',
                "sPrevious": '<i class="icon md-chevron-left"></i>'
            }
        }
    },
    api: function() {
        if (!$.fn.dataTable) return;
        if ($.fn.dataTable.TableTools) {
            // Set the classes that TableTools uses to something suitable for Bootstrap
            $.extend(true, $.fn.dataTable.TableTools.classes, {
                "container": "DTTT btn-group pull-left",
                "buttons": {
                    "normal": "btn btn-default btn-flat",
                    "disabled": "disabled"
                },
                "print": {
                    "body": "site-print DTTT_Print"
                }
            });
        }
    },
    init: function(context) {
        if (!$.fn.dataTable) return;
        var defaults = $.components.getDefaults("dataTable");
        $('[data-plugin="dataTable"]', context).each(function() {
            var options = $.extend(true, {}, defaults, $(this).data());
            $(this).dataTable(options);
        });
    }
});